const apiRequest = async (url, method = 'GET', body = null, headers = {}) => {
    // Token' localStorage veya baka bir yerden al
    
    
    const token = localStorage.getItem('token');
  
    if (token) {
      headers['Authorization'] = token;
    }
  
    const response = await fetch(url, {
      method,
      headers,
      body: body ? JSON.stringify(body) : null,
    });
  
    if (!response.ok) {
      throw new Error('API istei baarsz');
    }
  
    return response.json();
  };
  
  export default {
    get: (url, headers) => apiRequest(url, 'GET', null, headers),
    post: (url, body, headers) => apiRequest(url, 'POST', body, headers),
    // Dier HTTP metodlar iin benzer fonksiyonlar eklenebilir
  };
  