import React, { useState } from 'react';
import AuthService from '../../auth/AuthService';
import './Login.css';
import logo from '../../assets/logo.png';
import ErrorModal from '../ErrorModal/ErrorModal';
import { useNavigate } from 'react-router-dom';

function Login() {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [errorMessage, setErrorMessage] = useState('');
  const [showModal, setShowModal] = useState(false);
  let navigate = useNavigate();
  localStorage.setItem('authenticated', false);
  const handleSubmit = async (e) => {
    e.preventDefault();
    setErrorMessage(''); // Her giri denemesinde hata mesajn sfrla
    setShowModal(false);

    try {
      
      let trimmedUsername = username.trim();
      let trimmedPassword = password.trim();
      const response = await AuthService.login(trimmedUsername, trimmedPassword);
      localStorage.clear();
      debugger;
      console.log('Giri baarl, Token:', response.token);
      localStorage.setItem('isLoggedIn', true); // Giri yapldn localStorage'a kaydet
      localStorage.setItem('roles', response.roles); // Kullanc rollerini localStorage'a kaydet
      localStorage.setItem('username', response.username); // Kullanc adn localStorage'a kaydet
      localStorage.setItem('surname', response.surname); // Kullanc soyadn localStorage'a kaydet
      localStorage.setItem('name', response.name); // Kullanc adn localStorage'a kaydet
      localStorage.setItem('token', response.token); // Token' localStorage'a kaydet
     
      localStorage.setItem('authenticated', true);
      navigate('/dashboard');
    } catch (error) {
      console.error('Giri hatas', error);
      setErrorMessage(error.message); 
      setShowModal(true);
    }
  };

  return (
    <div className="login-container">
    <img src={logo} alt="Logo" className="login-logo" />
      <form onSubmit={handleSubmit} className="login-form">
        <input
          type="text"
          placeholder="Kullanc Ad"
          value={username}
          onChange={(e) => setUsername(e.target.value)}
        />
        <input
          type="password"
          placeholder="ifre"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        />
        <button type="submit">Giri Yap</button>
      </form>
      {showModal && <ErrorModal message={errorMessage} onClose={() => setShowModal(false)} />}
    </div>
  );
}

export default Login;
